/* contacts.css */
.contact-section {
  max-width: 800px;
  margin: 5rem auto;
  padding: 2rem;
  color: #fff;
  font-family: 'IBM Plex Mono', monospace;
  position: relative;
  border-radius: 15px;
}

.contact-title {
  font-size: 1.8rem;
  font-weight: bold;
  color: #fff;
  margin-bottom: 2rem;
  border-left: 6px solid #00ff6a;
  padding-left: 0.5rem;
  background: linear-gradient(to right, #00ff6a, transparent);
  display: inline-block;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-row {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.form-group {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;

  &.full {
    width: 100%;
  }
}

label {
  font-weight: bold;
  margin-bottom: 0.4rem;
}

input,
textarea {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 10px;
  color: #fff;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  font-family: inherit;
  transition: all 0.25s ease;
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}

input:hover,
textarea:hover {
  border-color: #3a82ff;
  box-shadow: 0 0 10px rgba(58, 130, 255, 0.5);
}

input:focus,
textarea:focus {
  outline: none;
  border-color: #3a82ff;
  box-shadow: 0 0 15px rgba(58, 130, 255, 0.75);
}

textarea {
  min-height: 140px;
  resize: vertical;
}

.send-button {
  align-self: center;
  margin-top: 1rem;
  background: rgba(158, 158, 158, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  padding: 0.75rem 2rem;
  color: #fff;
  font-family: inherit;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.send-button:hover {
  background: rgba(0, 95, 255, 0.6);
  box-shadow: 0 0 10px #005fff80;
  transform: translateY(-2px);
}

.email-display,
.contact-info-display {
  margin-top: 2rem;
  border-radius: 10px;
  padding: 1rem;
  text-align: center;
  font-family: inherit;
}

.email-display a,
.contact-info-display a {
  color: #fff;
  text-decoration: none;
}

.email-display a {
  border-bottom: 1px dashed #005fff;
}

.email-display a:hover,
.contact-info-display a:hover {
  color: #3a82ff;
}

.contact-info-display {
  display: flex;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.contact-info-display .contact-item {
  display: flex;
  align-items: center;
  gap: 0.3rem;
}

@media (max-width: 600px) {
  .contact-info-display {
    display: block;
    text-align: center;
  }

  .contact-info-display .contact-item {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.15rem;
    width: 100%;
    flex-shrink: 1;
  }

  .contact-info-display a {
    display: block;
    white-space: nowrap;
    font-size: clamp(0.85rem, 4vw, 1rem);
  }
  .contact-info-display .contact-item + .contact-item {
    margin-top: 1rem;
  }
}


