/* ProjectList.css */
.ProjectsList {
  padding: 2rem;
  color: #ffffff;
  text-align: center;
  position: relative;
  z-index: 10;
}

.ProjectsList h2 {
  font-size: 2rem;
  margin-bottom: 1.5rem;
  padding-top: 50px;
}

.projects-container {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
  justify-content: center;
}

.project-card {
  -webkit-backdrop-filter: blur(50px);
  backdrop-filter: blur(50px);
  border-radius: 10px;
  padding: 1.5rem;
  max-width: 300px;
  text-align: left;
  transition: transform 0.3s;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  position: relative;
}

.project-card:hover {
  transform: translateY(-5px);
}

.project-card .project-image {
  width: 100%;
  border-radius: 8px;
  margin-bottom: 1rem;
}

.project-card h3 {
  font-size: 1.25rem;
  margin: 0.5rem 0;
}

.project-card p {
  font-size: 0.9rem;
  color: #a3a3a3;
}

.project-card .tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin: 1rem 0;
}

.project-card .tech-stack .tech {
  background-color: #374151;
  color: #e5e7eb;
  font-size: 0.75rem;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
}

.project-card .repo-link {
  display: inline-block;
  margin-top: 1rem;
  margin-right: 0.5rem;
  padding: 0.5rem 1rem;
  color: #ffffff;
  background-color: #2563eb;
  border-radius: 5px;
  text-decoration: none;
  transition: background-color 0.3s;
}

.project-card .repo-link:hover {
  background-color: #2564eb7b;
  border-color: rgba(255, 255, 255, 0.5);
  box-shadow: 0 2px 8px rgba(255, 255, 255, 0.2);
}
